export interface DateRange {
    startDate: Date;
    endDate: Date;
}

export interface DateRangeStrings {
    from: string;
    to: string;
    lockEndDateToNow?: boolean;
}

export enum DateFormat {
    SHORT = 'hh:mm A MMM DD',
    STANDARD = 'DD/MM/YYYY, HH:mm:ss', // displayed in 24hr time
    YEAR_MONTH_DAY = 'YYYY/MM/DD',
    FULL_MONTH = 'MMMM D YYYY, h:mm a', // displayed in 12hr time
    HOURS_ONLY = 'hh:00 a',
    HOURS_MINUTES_SECONDS = 'HH:mm:ss',
    NOW = 'hh:mm a',
}

export enum TimeRangeDisplay {
    // Predefined ranges that the user can select
    ONE_HOUR = '1h',
    TWELVE_HOURS = '12h',
    TWENTY_FOUR_HOURS = '24h',
    SEVEN_DAYS = '7d',
    // Indicates selection of custom start/end dates
    CUSTOM = 'custom',
    ALL = 'all',
}

export enum TimeRangesInHours {
    '1h' = 1,
    '12h' = 12,
    '24h' = 24,
    '7d' = 168,
}

export const MIN_DATE: Date = new Date('1985-01-01T00:00:00');

export const DEFAULT_TIME_RANGE = TimeRangeDisplay.TWENTY_FOUR_HOURS;
